@-webkit-keyframes slide {
  0% {
    opacity: 0;
    transform: translateX(-15px);
  }
  20% {
    opacity: 1;
    transform: translateX(-9px);
  }
  80% {
    opacity: 1;
    transform: translateX(9px);
  }
  100% {
    opacity: 0;
    transform: translateX(10px);
  }
}
@keyframes slide {
  0% {
    opacity: 0;
    transform: translateX(-15px);
  }
  20% {
    opacity: 1;
    transform: translateX(-9px);
  }
  80% {
    opacity: 1;
    transform: translateX(9px);
  }
  100% {
    opacity: 0;
    transform: translateX(10px);
  }
}
